        $(".slider").slider({
            value:0,
            min: 0,
            max: 100,
            slide: function (event, ui) {
                answ = ui.value/100;
                $container.find(".efdsasw").append(answ+","); 

                var seuil = ui.value / 100;
              
                if (seuil < 0.26) {
                    $smallfleche.show();
                    $fleche1.show();
                    $mfleche.hide();
                    $m2fleche.hide();
                    $bigfleche.hide();
                }
                else if (seuil > 0.25 && seuil < 0.51) {
                    $smallfleche.hide();
                    $fleche1.hide();
                    $mfleche.show();
                    $m2fleche.hide();
                    $bigfleche.hide();
                }
                else if (seuil > 0.5 && seuil < 0.76) {
                    $smallfleche.hide();
                    $fleche1.hide();
                    $mfleche.hide();
                    $m2fleche.show();
                    $bigfleche.hide();
                }
                else if (seuil > 0.75) {
                    $smallfleche.hide();
                    $fleche1.hide();
                    $mfleche.hide();
                    $m2fleche.hide();
                    $bigfleche.show();
                }

                var opaco = seuil - 0.1;
                $container.find(".atmos").css("background-color", "rgba(255, 255, 255," + opaco + ")");
            }
        });

        var $ranger = $container.find(".ranger");
        var $smallfleche = $container.find(".smallfleche");
        var $fleche1 = $container.find(".fleche1");
        var $mfleche = $container.find(".mfleche");
        var $m2fleche = $container.find(".m2fleche");
